.App {
    /* background-image: url('pagebackground.jpg'); */
    position: absolute;
    left: 0;
    right: 0;
    top: 10px;
    bottom: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.pageForm {
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 500px;
  margin: 10px auto;
  font-weight: bold;
}

li {
  list-style: none;
  margin: 10px;
  padding: 10px;
}

lable {
  width: 200px;
  display: inline-block;
}

.loadingBlock {
  position: absolute;
  top: 40%;
  left: 50%;
}

header {
  background-color: #11b67a;
  height: 100px;
}

.logoSvg {
  position: absolute;
  top: 25px;
  left: 20px;
}

.formContainer {
  display: flex;
  justify-content: center;
}

.formContainer > div {
  margin: 20px;
}

.subTitle {
  font-weight: bold; 
}

.formContainer > div div:first-child {
  margin-bottom: 10px;
}